/* spinner */
.spinner {
  width: 55px;
  height: 55px;
  position: relative;
}
.spinner div {
  box-sizing: border-box;
  position: absolute;
  width: 100%;
  height: 100%;
  border: 8px solid transparent;
  border-top-color: #4257b2;
  border-radius: 50%;
  -webkit-animation: spinnerOne 1.2s linear infinite;
          animation: spinnerOne 1.2s linear infinite;
}
.spinner div:nth-child(2) {
  border: 8px solid transparent;
  border-bottom-color: #4257b2;
  -webkit-animation: spinnerTwo 1.2s linear infinite;
          animation: spinnerTwo 1.2s linear infinite;
}

@-webkit-keyframes spinnerOne {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); border-width: 10px; }
  50% { -webkit-transform: rotate(180deg); transform: rotate(180deg); border-width: 1px; }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); border-width: 10px; }
}

@keyframes spinnerOne {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); border-width: 10px; }
  50% { -webkit-transform: rotate(180deg); transform: rotate(180deg); border-width: 1px; }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); border-width: 10px; }
}
@-webkit-keyframes spinnerTwo {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); border-width: 1px; }
  50% { -webkit-transform: rotate(180deg); transform: rotate(180deg); border-width: 10px; }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); border-width: 1px; }
}
@keyframes spinnerTwo {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); border-width: 1px; }
  50% { -webkit-transform: rotate(180deg); transform: rotate(180deg); border-width: 10px; }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); border-width: 1px; }
}

